
<style scoped>
.inline-input {
    display: inline-block !important;
    
}
</style>


<template>
    <div>
        <el-card>
            <el-row>
                <el-col :span='8' >
                    <el-input placeholder='请输入搜索内容' v-model="id_for_seek">
                        <el-button slot="append" icon="el-icon-search" @click="seekbyid"></el-button>
                    </el-input>
                </el-col>
                <el-col :span='4'>
                    <el-button type='primary' @click="show_add_board=true">添加用户</el-button>
                </el-col>
            </el-row>
            <div v-if="table_obj">
                <el-table
                    :data='table_obj'
                >
                    <el-table-column type='index' label="#" />
                    <el-table-column prop='username' label="姓名" />
                    <el-table-column prop='email' label="邮箱" />
                    <el-table-column prop='mobile' label="电话" />
                    <el-table-column prop='role_name' label="角色" />
                    <el-table-column prop='mg_state' label='状态'>
                        <template slot-scope="scoped">
                            <el-switch 
                                v-model="scoped.row.mg_state"
                                @change='(v)=>{change_mg_state(v,scoped.row,scoped.$index)}'
                            ></el-switch>
                        </template>
                    </el-table-column>
                    <el-table-column prop='option' label="操作" >
                        <div slot-scope="scoped">
                            <el-button type='primary' icon="el-icon-edit" @click="run_editor(scoped.row,'edit')"></el-button>
                            <el-button type='danger' icon='el-icon-delete' @click="run_editor(scoped.row,'del')"></el-button>
                            <el-button type='warning' icon="el-icon-setting" @click="run_editor(scoped.row,'set')"></el-button>
                        </div>
                    </el-table-column>
                </el-table>
            </div>

            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="current_page"
                :page-sizes="page_sizes"
                :page-size="page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>

        <!-- 添加新管绿源 -->
        <el-dialog 
            title="添加用户" 
            :visible='show_add_board'
        >
            <div>
                用户名：<el-input required v-model="newuser.name"  type='text' />
                密码：<el-input required v-model="newuser.sn"  type='password' />
                邮箱：<el-input required v-model="newuser.email"  type='text' />
                手机：<el-input required v-model="newuser.num"  type='text' />
            </div>
            <div slot="footer">
                <el-button @click="show_add_board=false">取消</el-button>
                <el-button type='primary' @click="adduser">确定</el-button>
            </div>
        </el-dialog>
        <!-- 修改用户信息 面板 -->
        <el-dialog title="修改用户信息"
            :visible='show_edit_board'
        >
            <div>
                <el-input disabled v-model="edit_user.name">
                    <div slot="prepend">用户名</div>
                </el-input>
                <el-input required v-model="edit_user.email">
                    <div slot="prepend">邮箱</div>
                </el-input>
                <el-input required v-model="edit_user.mobile">
                    <div slot="prepend">手机</div>
                </el-input>
            </div>
            <div slot="footer">
                <el-button @click="show_edit_board=false">取消</el-button>
                <el-button type='primary' @click="edituser(timly_row)">确定</el-button>
            </div>
        </el-dialog>
        <!-- 删除用户面板 -->
        <el-dialog title="删除用户"
            :visible='show_del_board'
        >
            <div slot="footer">
                <el-button @click="show_del_board=false">取消</el-button>
                <el-button type='primary' @click='deleteuser(timly_row)' >确定</el-button>
            </div>
        </el-dialog>

        <!-- 分配角色面板 -->
        <el-dialog title="分配角色"
            :visible='show_role_board'
        >
            <div v-if="timly_row">
                <p>当前的用户名{{ now_role.name }}</p>
                <p>当前的角色{{ now_role.role }}</p>
                <p>
                    新的角色
                    <el-select v-model="now_role.newrole" v-if="rolelist">
                        <el-option
                            v-for='(m,i) in rolelist' :key='i'
                            :value='m.roleName'
                        >
                            {{ m.roleName }}
                        </el-option>
                    </el-select>
                </p>
            </div>
            <div slot="footer">
                <el-button @click="show_role_board=false">取消</el-button>
                <el-button type='primary' @click="settinguser(timly_row)">确定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
export default {
    name:'userlist',
    data() {
        return {
            // 列表信息
            table_obj:null,
            // 分页信息
            current_page:1,
            page_sizes:[2,5,10,15],
            page_size:2,
            total:3,
            // 添加用户 开关
            show_add_board:false,
            // 添加新用户
            newuser:{
                name:'',
                sn:'',
                email:'',
                num:'',
            },
            // 搜索的id（全等搜索）
            id_for_seek:'',
            // 编辑面板 开关
            show_edit_board:false,
            // 修改用户信息
            edit_user:{
                name:'',
                email:'',
                mobile:''
            },
            // 删除面板 开关
            show_del_board:false,
            // 分配角色 面板 开关
            show_role_board:false,
            // 记录点击项信息
            timly_row:null,
            // 分配角色
            now_role:{
                name:'',
                role:'',
                newrole:'',
                rid:''
            },
            rolelist:null,
        };
    },
    created(){

    },
    mounted(){
        window.userlist=this
        this.oninit()
    },
    //props:{},//[]
    methods:{
        async oninit(){
            let {data:res}=await this.$http.get(
                `users`,{params:{
                    query:'',
                    pagenum:this.current_page,
                    pagesize:this.page_size
                }}
            )
            this.table_obj=res.data.users
            console.log('table_obj',this.table_obj)
            this.total=res.data.total
        },
        async change_mg_state(v,r,i){
            console.log(v,r,i)
            let {data:res}=await this.$http.put(
                `/users/${r.id}/state/${v}`
            )
            console.log('修改状态结果',res)
            if(res.meta.status==200){
                this.oninit()
            }

        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size=val
            this.oninit()
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.current_page=val
            this.oninit()
        },
        async adduser(){
            let nu= this.newuser
            let boo=nu.name.trim()&&nu.sn.trim()&&nu.email.trim()&&nu.num.trim()
            if(!boo){
                this.$message('请补全信息')
                return false;
            }else{
                let {data:res}=await this.$http.post(
                    `/users`,{
                        username:nu.name,
                        password:nu.sn,
                        email:nu.email,
                        mobile:nu.num,
                    }
                )
                // console.log(res)
                if(res.meta.msg=='创建成功'){
                    this.show_add_board=false
                    this.oninit()
                }
            }
        },
        async seekbyid(){
            if(!this.id_for_seek.trim()){
                this.$message('搜索框为空')
                return false
            }else{
                // 全等搜索
                let {data:res}=await this.$http.get(
                    `/users`,{
                        params:{
                            query:this.id_for_seek,
                            pagenum:this.current_page,
                            pagesize:this.page_size,
                        }
                    }
                )
                console.log(res)
                if(res.data){
                    this.table_obj=res.data.users
                    console.log('table_obj',this.table_obj)
                    this.total=res.data.total
                }else{
                    this.$message(res.meta.msg)
                }
            }
        },
        async getrolesinfo(cb){
            let {data:res}=await this.$http.get(
                `/roles`
            )
            console.log('获取角色列表',res)
            this.rolelist=res.data
            cb()
        },
        run_editor(r,str){
            this.timly_row=r
            switch(str){
                case 'edit':
                    let te=this.edit_user
                    te.name=r.username
                    te.email=r.email
                    te.mobile=r.mobile
                    this.show_edit_board=true
                break;
                case 'del':
                    this.show_del_board=true
                break;
                case 'set':
                    this.getrolesinfo(()=>{
                        this.show_role_board=true
                    })
                break;
            }
        },
        async edituser(r){
            let {data:{data:res}}=await this.$http.put(
                `/users/${r.id}`,{
                    email:r.email,
                    mobile:r.mobile
                }
            )
            console.log(res)
            this.show_edit_board=false
            this.oninit()
        },
        async deleteuser(r){
            let {data:res}=await this.$http.delete(
                `/users/${r.id}`
            )
            console.log('删除回执',res)
            this.show_del_board=false
            this.oninit()

        },
        async settinguser(r){
            this.now_role.rid=
            this.rolelist.find(m=>{
                return m.roleName==this.now_role.newrole
            }).id
            let {data:res}=await this.$http.put(
                `/users/${r.id}/role`,{
                    rid:this.now_role.rid
                }
            )
            console.log(res)
            this.show_role_board=false
            this.oninit()
        },
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>